#wrapper {
    -webkit-overflow-scrolling: touch;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: var(--viewport-height);
    overflow: hidden;
    position: relative;
    z-index: 2;
}

#main {
    width: auto;
    --alignment: center;
    --flex-alignment: center;
    --indent-left: 1;
    --indent-right: 1;
    --border-radius-tl: 0;
    --border-radius-tr: 0;
    --border-radius-br: 0;
    --border-radius-bl: 0;
    align-items: center;
    display: flex;
    flex-grow: 0;
    flex-shrink: 0;
    justify-content: center;
    max-width: 100%;
    position: relative;
    text-align: var(--alignment);
    z-index: 1;
    transition: opacity 1s ease-in-out 0s;
}

#main > .inner {
    --padding-horizontal: 1rem;
    --padding-vertical: 1.25rem;
    --spacing: 1.125rem;
    --width: 37rem;
    border-radius: var(--border-radius-tl) var(--border-radius-tr) var(--border-radius-br) var(--border-radius-bl);
    max-width: 100%;
    position: relative;
    width: var(--width);
    z-index: 1;
    padding: var(--padding-vertical) var(--padding-horizontal);
}

#main > .inner > header {
    margin-bottom: var(--spacing);
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
}

#main > .inner > footer {
    margin-top: var(--spacing);
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
}

#main > .inner > * > * {
    margin-top: var(--spacing);
    margin-bottom: var(--spacing);
}

#main > .inner > * > :first-child {
    margin-top: 0 !important;
}

#main > .inner > * > :last-child {
    margin-bottom: 0 !important;
}

#main > .inner > .full {
    margin-left: calc(var(--padding-horizontal) * -1);
    max-width: calc(100% + calc(var(--padding-horizontal) * 2) + 0.4725px);
    width: calc(100% + calc(var(--padding-horizontal) * 2) + 0.4725px);
}

#main > .inner > .full:first-child {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    margin-top: calc(var(--padding-vertical) * -1) !important;
}

#main > .inner > .full:last-child {
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
    margin-bottom: calc(var(--padding-vertical) * -1) !important;
}

#main > .inner > .full.screen {
    border-radius: 0 !important;
    max-width: 100vw;
    position: relative;
    width: 100vw;
    left: 50%;
    margin-left: -50vw;
    right: auto;
}

#main > .inner > * > .full {
    margin-left: calc(-1rem);
    max-width: calc(100% + 2rem + 0.4725px);
    width: calc(100% + 2rem + 0.4725px);
}

#main > .inner > * > .full.screen {
    border-radius: 0 !important;
    max-width: 100vw;
    position: relative;
    width: 100vw;
    left: 50%;
    margin-left: -50vw;
    right: auto;
}

#main > .inner > header > .full:first-child {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    margin-top: -1.25rem !important;
}

#main > .inner > footer > .full:last-child {
    margin-bottom: -1.25rem !important;
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
}

body.is-loading #main {
    opacity: 0;
}

#main > .inner > header, #main > .inner > footer {
    transition: opacity 0.25s ease-in-out 0.375s,visibility 0.25s linear 0.375s;
}

#main > .inner > header.hidden, #main > .inner > footer.hidden {
    transition: opacity 0.25s ease-in-out, visibility 0.25s;
    opacity: 0;
    visibility: hidden;
}

#main > .inner > section {
    transition: opacity 0.5s ease-in-out 0.25s,min-height 0.25s ease-in-out, max-height 0.25s ease-in-out;
}

#main > .inner > section.inactive {
    opacity: 0;
    transition: opacity 0.25s ease-in-out;
}

body.is-instant #main, body.is-instant #main > .inner > *,body.is-instant #main > .inner > section > * {
